<h2 id="http" tabindex="-1"><a class="header-anchor" href="#http" aria-hidden="true">#</a> HTTP</h2>
<h3 id="question-1-1p" tabindex="-1"><a class="header-anchor" href="#question-1-1p" aria-hidden="true">#</a> Question 1 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Name the method one should use in HTTP when the request is about:</p>
<ol>
