<h3 id="question-2-1p" tabindex="-1"><a class="header-anchor" href="#question-2-1p" aria-hidden="true">#</a> Question 2 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Specify the status code that should be sent back in HTTP when:</p>
<ol>
<li>The code on the server crashes</li>
<li>The request was successfully carried out, and the requested resource is sent back in the body of the response</li>
<li>The URI in the request identifies a resource that doesn't exist</li>
</ol>
<p>You will get:</p>
<ul>
<li>0.33 points for each correct status code</li>
</ul>
</template>
<template v-slot:sample-answer>
