<h3 id="question-7-1p" tabindex="-1"><a class="header-anchor" href="#question-7-1p" aria-hidden="true">#</a> Question 7 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Below is a small part of the HTML code found on a webpage.</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>p</span><span class="token punctuation">></span></span>Arsenal is no longer a good football team (>_&lt;) Once they were the best team in all of England.<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>p</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>Explain what's wrong with the HTML code, and suggest how it can be improved.</p>
</template>
<template v-slot:sample-answer>
<p>The problem with the HTML code is that <code>&gt;</code> and <code>&lt;</code> are used as text characters, which they shouldn't be, since they have special meaning in HTML (to indicate the start and end of tags). These should instead be expressed as their character entities, i.e. as <code>&amp;gt;</code> and <code>&amp;lt;</code> instead.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-8-1p" tabindex="-1"><a class="header-anchor" href="#question-8-1p" aria-hidden="true">#</a> Question 8 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>David has the following HTML code on a webpage (<code>[...]</code> means that not everything is shown):</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code>[...]

<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>p</span><span class="token punctuation">></span></span>I grow up in Habo, where nothing interesting ever happens. But I managed to survive and [...]<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>p</span><span class="token punctuation">></span></span>

<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>p</span><span class="token punctuation">></span></span>When I got my first job and had earned some money, I quickly moved to a better village where [...]<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>p</span><span class="token punctuation">></span></span>

[...]
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><p>When he looks at the webpage in a web browser, he realizes he wants to have more space between the paragraphs, so he adds two <code>&lt;br&gt;</code> elements between the paragraphs in the HTML code. After that it looks good in the web browser.</p>
<p>Is using two <code>&lt;br&gt;</code> elements a good solution in this case? If yes, why? If no, why not, and what would be a good solution?</p>
</template>
<template v-slot:sample-answer>
<p>No, it's not good a solution to use two <code>&lt;br&gt;</code> elements, because their semantic meaning is to add line breaks to text. In this case, he doesn't want to have more line breaks in the text, but bigger gap between the paragraphs. Adding more vertical margins to the paragraphs elements (using CSS) is a better solution.</p>
</template>
<template v-slot:marking-guidelines>
</template>
</ExamQuestion>
