<ExamQuestion>
<template v-slot:question>
<p>Explain what <code>hover</code> is in CSS.</p>
</template>
<template v-slot:sample-answer>
<p><code>hover</code> is a pseudo class in CSS, which is a special class an element has when the element is in a certain state. An element gets the <code>hover</code> pseudo class when the user has the mouse cursor over the element.</p>
</template>
<template v-slot:marking-guidelines>
</template>
</ExamQuestion>
