<h2 id="website" tabindex="-1"><a class="header-anchor" href="#website" aria-hidden="true">#</a> Website</h2>
<h3 id="question-13-1p" tabindex="-1"><a class="header-anchor" href="#question-13-1p" aria-hidden="true">#</a> Question 13 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>On a website, an admin can submit a <code>&lt;form&gt;</code> to the server, which then in turn will send a news letter email to all the website's users. When the <code>&lt;form&gt;</code> is submitted, which method should the HTTP request to the server have? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>The <code>&lt;form&gt;</code> should be submitted with the <code>POST</code> method, since the goal of the request is to achieve a side-effect on the server (to send emails). The other option is to use the <code>GET</code> method, but that's not suitable since <code>GET</code> requests should not have side-effects.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-14-1p" tabindex="-1"><a class="header-anchor" href="#question-14-1p" aria-hidden="true">#</a> Question 14 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Alice has implemented her own website in Express. After she has published it on the web and it has been running for 2 months, it starts to take long time for her to get back responses from it. She's thinking it might be because many other people on the web has discovered and started to use her website too. To see if that's the case, she wants to change the source code to make it log the URI and method of all incoming HTTP requests, but she doesn't know how to add that functionality.</p>
<p>Explain to Alice what a good way is to add such functionality to her Express application. You don't need to write any code, just a general explanation.</p>
</template>
<template v-slot:sample-answer>
<p>Alice needs to add a middleware function to her Express application that runs for all incoming HTTP request. In this middleware she needs to look at the request object to figure out which URL and method that are used in the request, and log them, either using <code>console.log()</code> and redirect the output from the application to a file, or by writing the URL and method directly to a file (one line for each HTTP request).</p>
</template>
<template v-slot:marking-guidelines>
</template>
</ExamQuestion>
<h3 id="question-15-1p" tabindex="-1"><a class="header-anchor" href="#question-15-1p" aria-hidden="true">#</a> Question 15 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>An Express application consists of a sequence of middlewares. Does the order of the middlewares matter? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>Yes, often the middlewares aren't independent of each other, and then the order matters. For example, a middleware making use of the user's session must come after the middleware that populates the request object with the user's session.</p>
</template>
<template v-slot:marking-guidelines>
</template>
</ExamQuestion>
<h3 id="question-16-2p" tabindex="-1"><a class="header-anchor" href="#question-16-2p" aria-hidden="true">#</a> Question 16 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>On Bob's website, whenever someone tries to access a page that only a logged in user should be able to access, he currently sends back HTML code showing the text <code>You need to be logged in to view this page.</code> to the user. He wants to change this to instead take the user to the login page, and after the user has successfully logged in there, the user should be taken back to the page the user initially tried to access, whichever that is.</p>
<p>Bob is not sure how to implement this new functionality. Explain to Bob which changes he needs to make to his current implementation to make it work as he wants. Be as specific as possible, but since you don't know which language or framework he is using, you can't say any details about those things, so just describe the general procedure in detail.</p>
<p>If you need to make some assumptions about his current solution to write a good answer, then clearly describe which assumptions you make.</p>
</template>
<template v-slot:sample-answer>
<p>When a user is not logged and tries to access a page only logged in users should be able to access, he needs to send back a redirect response that redirects the user to the login page. He also needs to remember which page he should redirect the user back to after the user has successfully logged in. He can do that by adding querystring parameter in the redirection, e.g. <code>/login?redirectUrl=THE_URL_TO_THE_PAGE_THE_USER_WANTED_TO_ACCESS</code>. Then, when the user successfully logs in, he needs to redirect the user back to the page indicated by the <code>redirectUrl</code> parameter by sending back a redirection response.</p>
</template>
<template v-slot:marking-guidelines>
</template>
</ExamQuestion>
<h2 id="security" tabindex="-1"><a class="header-anchor" href="#security" aria-hidden="true">#</a> Security</h2>
<h3 id="question-17-1p" tabindex="-1"><a class="header-anchor" href="#question-17-1p" aria-hidden="true">#</a> Question 17 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Hashing is overall more secure than encryption. HTTPS makes use of encryption. Is it a good idea to replace encryption in HTTPS with hashing in an attempt to make HTTPS even more secure? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No, it's not a good idea, because in HTTPS, the goal is to make something readable again after it has been encrypted/hashed, but if you use hashing, you can't (easily) make it readable again, since hashing is an operation that hasn't been designed to be reversible.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-18-1p" tabindex="-1"><a class="header-anchor" href="#question-18-1p" aria-hidden="true">#</a> Question 18 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Using bcrypt, the hash value of:</p>
<ul>
<li><code>fhjr</code> is <code>$2a$12$n2X0JVB1xhE9riOMSkNvtuFsDnkPJJcfvBJw3.a1p/NyVds8.pxXG</code></li>
<li><code>skruqpldntrjeska</code> is <code>$2a$12$4JXX/1a3mlM4tUqnYkxgq.tHxQ/dfmL1nQK458MWjuh5NrN0sKGa.</code></li>
</ul>
<p>Looking at the two hash values, figuring out which the original passwords are seems quite hard in both cases, but one of them is much easier than the other. Which one is easier to figure out, and why is that one easier to figure out than the other?</p>
</template>
<template v-slot:sample-answer>
<p>To figure out the original password, a hacker can simply try to hash (using bcrypt) different strings, and see if any of them has the same hash value as the sought one. When a match is found, the hacker knows which the original password is (the random string she just hashed).</p>
<p>The password for <code>$2a$12$n2X0JVB1xhE9riOMSkNvtuFsDnkPJJcfvBJw3.a1p/NyVds8.pxXG</code> will be much easier to figure out this way since the original password for this one is much shorter (consists of only 4 characters). A hacker will be able to much faster compute the hash values for all strings consisting of 4 characters compared to all strings consisting of 16 characters.</p>
</template>
<template v-slot:marking-guidelines>
</template>
</ExamQuestion>
<h3 id="question-19-2p" tabindex="-1"><a class="header-anchor" href="#question-19-2p" aria-hidden="true">#</a> Question 19 (2p)</h3>
