</template>
</ExamQuestion>
<h3 id="question-23-2p" tabindex="-1"><a class="header-anchor" href="#question-23-2p" aria-hidden="true">#</a> Question 23 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In JavaScript, a movie with a title and a runtime (in minutes), can be represented as an object like this:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> movie <span class="token operator">=</span> <span class="token punctuation">{</span>
    title<span class="token operator">:</span> <span class="token string">"Shrek"</span><span class="token punctuation">,</span>
    runtimeInMinutes<span class="token operator">:</span> <span class="token number">98</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><p>Implement the function <code>getLongMovies()</code>, which receives an array with movie objects as argument, and that returns a new array containing only the movie objects that are strictly longer than the average runtime of the movies it receives.</p>
<p>If it's suitable to create helper functions, do that.</p>
</template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">getLongMovies</span><span class="token punctuation">(</span><span class="token parameter">movies</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> averageRuntimeInMinutes <span class="token operator">=</span> <span class="token function">getAverageRuntimeInMinutes</span><span class="token punctuation">(</span>movies<span class="token punctuation">)</span>
    <span class="token keyword">const</span> longMovies <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span>
    
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> movie <span class="token keyword">of</span> movies<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">if</span><span class="token punctuation">(</span>averageRuntimeInMinutes <span class="token operator">&lt;</span> movie<span class="token punctuation">.</span>runtimeInMinutes<span class="token punctuation">)</span><span class="token punctuation">{</span>
            longMovies<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span>movie<span class="token punctuation">)</span>
        <span class="token punctuation">}</span>
    <span class="token punctuation">}</span>
    
    <span class="token keyword">return</span> movies
    
<span class="token punctuation">}</span>

<span class="token keyword">function</span> <span class="token function">getAverageRuntimeInMinutes</span><span class="token punctuation">(</span><span class="token parameter">movies</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">let</span> runtimeInMinutesSum <span class="token operator">=</span> <span class="token number">0</span>
    
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> movie <span class="token keyword">of</span> movies<span class="token punctuation">)</span><span class="token punctuation">{</span>
        runtimeInMinutesSum <span class="token operator">+=</span> movie<span class="token punctuation">.</span>runtimeInMinutes
    <span class="token punctuation">}</span>
    
    <span class="token keyword">const</span> averageRuntimeInMinutes <span class="token operator">=</span> runtimeInMinutesSum <span class="token operator">/</span> movies<span class="token punctuation">.</span>length
    
    <span class="token keyword">return</span> averageRuntimeInMinutes
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br></div></div></template>
