<h3 id="question-24-2p" tabindex="-1"><a class="header-anchor" href="#question-24-2p" aria-hidden="true">#</a> Question 24 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Create the function <code>createCounter()</code>, that receives two integers as arguments:</p>
<ul>
<li>The first argument represents the counter's start value</li>
<li>The second argument represents how much the counter should be incremented each time it should be incremented</li>
</ul>
<p>The function should create and send back a new function. When that new function is called, the counter should be incremented by the amount specified by the second argument, and the current counter value should then be returned.</p>
<p>Sample usage:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> counterA <span class="token operator">=</span> <span class="token function">createCounter</span><span class="token punctuation">(</span><span class="token number">0</span><span class="token punctuation">,</span> <span class="token number">1</span><span class="token punctuation">)</span>
<span class="token function">counterA</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token comment">// --> 1</span>
<span class="token function">counterA</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token comment">// --> 2</span>
<span class="token function">counterA</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token comment">// --> 3</span>
<span class="token keyword">const</span> counterB <span class="token operator">=</span> <span class="token function">createCounter</span><span class="token punctuation">(</span><span class="token number">10</span><span class="token punctuation">,</span> <span class="token number">2</span><span class="token punctuation">)</span>
<span class="token function">counterB</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token comment">// --> 12</span>
<span class="token function">counterB</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token comment">// --> 14</span>
<span class="token function">counterA</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token comment">// --> 4</span>
<span class="token function">counterB</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token comment">// --> 16</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div></template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">createCounter</span><span class="token punctuation">(</span><span class="token parameter">startValue<span class="token punctuation">,</span> incrementStep</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">let</span> counter <span class="token operator">=</span> startValue
    
    <span class="token keyword">return</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        counter <span class="token operator">+=</span> incrementStep
        <span class="token keyword">return</span> counter
    <span class="token punctuation">}</span>
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div></template>
